export default {
    data() {
        return {
            email: '',
            loadingState: false
        };
    },
    methods: {
        async sendMail() {
            try {
                this.loadingState = true;
                await this.$axios.post(
                    process.env.BASE_URL + '/reset-password',
                    {
                        email: this.email
                    }
                );
                this.loadingState = false;
            } catch (error) {}
        }
    }
};
